import { Component, OnInit, OnDestroy } from '@angular/core';
import { Store } from '@ngrx/store';
import { AppState } from '../../../../interfaces';
import { UserActions } from '../../../actions/user.actions';
import { UserService } from '../../../services/user.service';
import { Order } from '../../../../core/models/order';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { Observable } from 'rxjs/Observable';
import { environment } from '../../../../../environments/environment';
import { LineItem } from '../../../../core/models/line_item';
import { CheckoutActions } from '../../../../checkout/actions/checkout.actions'
import { getAdjustmentOrder } from '../../../../checkout/reducers/selectors'
import { getUserOrders } from '../../../reducers/selector';

@Component({
  selector: 'app-order-detail',
  templateUrl: './order-detail.component.html',
  styleUrls: ['./order-detail.component.scss']
})
export class OrderDetailComponent implements OnInit, OnDestroy {
  routeSubscription$: Subscription;
  orderSubscription$: Subscription;
  childSubscription$: Subscription;
  orderNumber: String;
  order: Order;
  userOrder$: Observable<any>;

  constructor(
    private store: Store<AppState>,
    private checkout: CheckoutActions,
    private userService: UserService,
    private route: ActivatedRoute,
    private router: Router
  ) {
    this.userOrder$ = this.store.select(getUserOrders)
  }

  ngOnInit() {
    this.routeSubscription$ = this.route.params.subscribe(
      (params: any) => {
        this.orderNumber = params['number'];
        this.orderSubscription$ =
          this.userService
          .getOrderDetail(this.orderNumber)
          .subscribe(order => this.order = order);
     });
  }

  getProductImageUrl(line_item: LineItem) {
    if (line_item.variant.images[0]){
      const image_url = line_item.variant.images[0].small_url;
      return environment.API_ENDPOINT + image_url;
    }
    return environment.API_ENDPOINT;
  }

  createAdjOrder(line_item: any){
    // [routerLink]="['/product', line_item.order_info.adj_slug]"
    this.router.navigate(['/product', line_item.order_info.adj_slug]);
    this.store.dispatch(this.checkout.updateAdjustmentOrder(line_item.id.toString()));
  }

  caseView(id: any){
    // this.childSubscription$ = this.userOrder$.subscribe(res=>{
    //     let orders = res;
    //     orders.forEach(order => {
    //       if (order.original_line_item == id){
            this.router.navigate(['/user', 'orders', 'adjust', id]);
      //     }          
      //   });
      // });
  }

  ngOnDestroy() {
    this.routeSubscription$.unsubscribe();
    this.orderSubscription$.unsubscribe();
    if (this.childSubscription$)
      this.childSubscription$.unsubscribe();
  }

}



